'use client';

/**
 * TheGrid — Main node visualization container
 * Manages layout, pan/zoom, and node interactions
 */

import { useState, useRef, useEffect } from 'react';
import type { VisualNode } from '@/lib/types';
import NodeGlyph from './NodeGlyph';

interface TheGridProps {
  nodes: VisualNode[];
  selectedNodeId?: string;
  onNodeClick?: (node: VisualNode) => void;
  className?: string;
}

export default function TheGrid({
  nodes,
  selectedNodeId,
  onNodeClick,
  className = '',
}: TheGridProps) {
  const containerRef = useRef<HTMLDivElement>(null);
  const [pan, setPan] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [isPanning, setIsPanning] = useState(false);
  const [panStart, setPanStart] = useState({ x: 0, y: 0 });

  // Center view on mount
  useEffect(() => {
    if (!containerRef.current) return;

    const container = containerRef.current;
    const centerX = container.clientWidth / 2 - 400;
    const centerY = container.clientHeight / 2 - 300;

    setPan({ x: centerX, y: centerY });
  }, []);

  // Mouse/touch pan handlers
  const handleMouseDown = (e: React.MouseEvent) => {
    if (e.button !== 0) return; // Only left click
    setIsPanning(true);
    setPanStart({
      x: e.clientX - pan.x,
      y: e.clientY - pan.y,
    });
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!isPanning) return;
    setPan({
      x: e.clientX - panStart.x,
      y: e.clientY - panStart.y,
    });
  };

  const handleMouseUp = () => {
    setIsPanning(false);
  };

  // Zoom handler
  const handleWheel = (e: React.WheelEvent) => {
    e.preventDefault();
    const delta = e.deltaY * -0.001;
    setZoom((prev) => Math.min(Math.max(0.5, prev + delta), 2));
  };

  // Keyboard navigation
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      const step = 50;
      switch (e.key) {
        case 'ArrowUp':
          setPan((prev) => ({ ...prev, y: prev.y + step }));
          e.preventDefault();
          break;
        case 'ArrowDown':
          setPan((prev) => ({ ...prev, y: prev.y - step }));
          e.preventDefault();
          break;
        case 'ArrowLeft':
          setPan((prev) => ({ ...prev, x: prev.x + step }));
          e.preventDefault();
          break;
        case 'ArrowRight':
          setPan((prev) => ({ ...prev, x: prev.x - step }));
          e.preventDefault();
          break;
        case '0':
          setZoom(1);
          e.preventDefault();
          break;
        case '+':
        case '=':
          setZoom((prev) => Math.min(prev + 0.1, 2));
          e.preventDefault();
          break;
        case '-':
          setZoom((prev) => Math.max(prev - 0.1, 0.5));
          e.preventDefault();
          break;
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  // Get connected node IDs
  const selectedNode = nodes.find((n) => n.id === selectedNodeId);
  const connectedNodeIds = new Set(selectedNode?.connections || []);

  // Draw connection lines
  const renderConnections = () => {
    if (!selectedNode) return null;

    return selectedNode.connections?.map((connectedId) => {
      const connectedNode = nodes.find((n) => n.id === connectedId);
      if (!connectedNode) return null;

      return (
        <svg
          key={`connection-${selectedNode.id}-${connectedId}`}
          className="absolute inset-0 pointer-events-none"
          style={{
            width: '100%',
            height: '100%',
            overflow: 'visible',
          }}
        >
          <line
            x1={selectedNode.position.x}
            y1={selectedNode.position.y}
            x2={connectedNode.position.x}
            y2={connectedNode.position.y}
            stroke={selectedNode.signalVisuals.color}
            strokeWidth="2"
            strokeDasharray="5,5"
            opacity="0.4"
            className="animate-pulse"
          />
        </svg>
      );
    });
  };

  return (
    <div
      ref={containerRef}
      className={`relative w-full h-full overflow-hidden ${className}`}
      onMouseDown={handleMouseDown}
      onMouseMove={handleMouseMove}
      onMouseUp={handleMouseUp}
      onMouseLeave={handleMouseUp}
      onWheel={handleWheel}
      style={{
        cursor: isPanning ? 'grabbing' : 'grab',
      }}
    >
      {/* Grid container with transform */}
      <div
        className="absolute inset-0 transition-transform duration-100"
        style={{
          transform: `translate(${pan.x}px, ${pan.y}px) scale(${zoom})`,
          transformOrigin: '0 0',
        }}
      >
        {/* Connection lines */}
        {renderConnections()}

        {/* Nodes */}
        {nodes.map((node) => (
          <NodeGlyph
            key={node.id}
            node={node}
            isSelected={node.id === selectedNodeId}
            isConnected={connectedNodeIds.has(node.id)}
            onClick={() => onNodeClick?.(node)}
          />
        ))}
      </div>

      {/* Controls hint */}
      <div
        className="absolute bottom-4 left-4 px-6 py-4 rounded-lg
                   bg-gunmetal/80 backdrop-blur-sm border border-slate-light/20
                   text-xs font-mono text-slate-light"
      >
        <div className="mb-2">Drag to pan • Scroll to zoom • Arrow keys to navigate</div>
        <div className="opacity-60">
          Press 0 to reset zoom • +/- to zoom in/out
        </div>
      </div>

      {/* Zoom indicator */}
      <div
        className="absolute top-4 right-4 px-5 py-3 rounded-lg
                   bg-gunmetal/80 backdrop-blur-sm border border-slate-light/20
                   text-xs font-mono text-slate-light"
      >
        {Math.round(zoom * 100)}%
      </div>
    </div>
  );
}
